import React, { useState, useEffect } from 'react';
import { questions } from './SurveyQuestions';
import { Stack, Divider, Typography, Radio, RadioGroup, FormControlLabel, Button, TextField } from '@mui/material';
import { Formik } from 'formik';
import IntlMessages from '@jumbo/utils/IntlMessages';
import ChevronRightRoundedIcon from '@mui/icons-material/ChevronRightRounded';
import { getTagsByCallId, SubmitSurveyByID, sendMail } from 'api/api';
import SendIcon from '@mui/icons-material/Send';
import SaveIcon from '@mui/icons-material/Save';
import LoadingButton from '@mui/lab/LoadingButton';
import { NotificationManager } from 'react-notifications';
import { Box } from '@mui/system';
import { useIntl } from 'react-intl';
import { renderToStaticMarkup } from 'react-dom/server';
import MailBodySurvey from './MailBodySurvey';

export default function CallSurvey({ callId }) {
  const [startSurvey, setStartSurvey] = useState(false);
  const [survey, setSurvey] = useState([]);
  const [btnLoading, setBtnLoading] = useState(false);
  const [email, setEmail] = useState('');
  let curentLanguage = useIntl();

  const handleSendClick = () => {
    setBtnLoading(true);
    if (/\S+@\S+\.\S+/.test(email)) {
      let mailSubject =
        curentLanguage.locale === 'en-EN'
          ? 'Survey Result'
          : curentLanguage.locale === 'tr-TR'
          ? 'Anket Sonucu'
          : curentLanguage.locale === 'nl-NL'
          ? 'Enqu√™teresultaat'
          : curentLanguage.locale === 'de-DE'
          ? 'Umfrageergebnis'
          : 'Anket Sonucu';
      let score = survey[0].supervisorId;
      let rawdata = AnswersToObject(survey[0].note);
      let html = renderToStaticMarkup(<MailBodySurvey rawdata={rawdata} score={score} />);
      sendMail(email, mailSubject, html, true)
        .then(resp => {
          if (resp.status === 200) {
            if (resp.data.message === 'Send') {
              NotificationManager.success(<IntlMessages id={'sendingSuccess'} />, '', 3000);
              setEmail('');
              setBtnLoading(false);
            }
          } else {
            NotificationManager.error(resp.data.message, '', 3000);
            setBtnLoading(false);
          }
        })
        .catch(err => {
          NotificationManager.error(err.message, '', 3000);
          setBtnLoading(false);
        });
    } else {
      NotificationManager.warning(<IntlMessages id={'invalidemail'} />, '', 3000);
      setBtnLoading(false);
    }
  };
  const handleUpdate = () => {
    setSurvey([]);
    setStartSurvey(true);
  };
  useEffect(() => {
    getTagsByCallId(callId).then(resp => setSurvey(JSON.parse(resp.data.data)));
  }, [callId, btnLoading]);
  const questionsArray = questions.map(item => ({
    title: item.title,
    question: item.question,
    point: item.point,
    answer: '',
  }));
  const questionsObjFormik = questionsArray.reduce((a, value) => ({ ...a, [value.question]: '' }), {});
  return (
    <div style={{ marginTop: '1rem', textAlign: 'center' }}>
      {survey.length > 0 && survey[0]?.supervisorId !== '' ? (
        <Box
          sx={{
            '& .MuiTextField-root': {
              my: 1,
            },
            '&': {
              p: 1,
            },
          }}>
          <Typography variant="h5" sx={{ textAlign: 'center' }}>
            <IntlMessages id={'sendsurveyresulttoemail'} />
          </Typography>
          <Typography variant="h6" sx={{ textAlign: 'center' }}>
            <IntlMessages id={'surveyScore'} />
            {' = '}
            <b>{survey[0].supervisorId}</b>
          </Typography>
          <Button size="small" variant="text" sx={{ '&': { textTransform: 'unset' } }} onClick={handleUpdate}>
            <IntlMessages id={'restartsurvey'} />
          </Button>
          <TextField
            required
            fullWidth
            label={<IntlMessages id={'email'} />}
            type="email"
            value={email}
            onChange={e => setEmail(e.target.value)}
            variant="standard"
          />
          <LoadingButton
            size="large"
            fullWidth
            variant="contained"
            color="secondary"
            onClick={handleSendClick}
            endIcon={<SendIcon />}
            loading={btnLoading}
            loadingPosition="end"
            sx={{ mt: 1 }}>
            <IntlMessages id={'send'} />
          </LoadingButton>
        </Box>
      ) : !startSurvey ? (
        <Button
          fullWidth
          variant="contained"
          color="info"
          onClick={() => setStartSurvey(true)}
          endIcon={<ChevronRightRoundedIcon />}>
          <IntlMessages id={'startSurvey'} />
        </Button>
      ) : (
        <>
          <Typography variant="h4" component="div">
            <IntlMessages id={'survey'} />
          </Typography>
          <Formik
            initialValues={questionsObjFormik}
            onSubmit={values => {
              setBtnLoading(true);
              let score = 0;
              let answers = Object.values(values);
              let answersStr = JSON.stringify(
                answers.reduce((a, v, i) => ({ ...a, [i]: v === 'EVET' ? '1' : v === 'HAYIR' ? '0' : '-' }), {}),
              )
                .replaceAll('"', '')
                .replace('{', '')
                .replace('}', '');

              for (let i = 0; i < questionsArray.length; i++) {
                questionsArray[i].answer = answers[i];
                if (answers[i] === 'EVET') {
                  if (questionsArray[i].title === 'SurveyTitle13') {
                    score *= questionsArray[i].point;
                  } else {
                    score += questionsArray[i].point;
                  }
                }
              }
              SubmitSurveyByID(callId, score, answersStr)
                .then(resp => resp.success && setBtnLoading(false))
                .catch(err => {
                  NotificationManager.error(err.message, '', 3000);
                  setBtnLoading(false);
                });
            }}>
            {({ handleSubmit, handleChange }) => (
              <form onSubmit={handleSubmit}>
                <Stack spacing={2} divider={<Divider orientation="horizontal" flexItem />}>
                  {questionsArray.map((item, index) => (
                    <div key={index}>
                      <Typography variant="h5" component="div">
                        <IntlMessages id={item.title} />
                      </Typography>
                      <Typography variant="h6" component="div">
                        <b>{`${index + 1}) `}</b>
                        <span>
                          <IntlMessages id={item.question} />
                        </span>
                      </Typography>
                      <RadioGroup style={{ justifyContent: 'center' }} row name={item.question} onChange={handleChange}>
                        <FormControlLabel value="EVET" control={<Radio />} label={<IntlMessages id={'yes'} />} />
                        <FormControlLabel value="HAYIR" control={<Radio />} label={<IntlMessages id={'no'} />} />
                      </RadioGroup>
                    </div>
                  ))}
                </Stack>
                <LoadingButton
                  size="large"
                  fullWidth
                  type="submit"
                  variant="contained"
                  loading={btnLoading}
                  loadingPosition="start"
                  startIcon={<SaveIcon />}
                  sx={{ mt: 1 }}>
                  <IntlMessages id={'save'} />
                </LoadingButton>
              </form>
            )}
          </Formik>
        </>
      )}
    </div>
  );
}

//dbden gelen soru-cevap stringini tekrardan objecte d√∂n√º≈üt√ºrmek i√ßin kullanƒ±rƒ±z. doƒürudan object dbye yollanmƒ±yor.
//JSON.parse ederken sorun olu≈üuyor √ß√ºnk√º o field string t√ºr√ºnde ve object "{  }" ≈üeklinde yazƒ±ldƒ±ƒüƒ±ndan parse edemiyor
const AnswersToObject = str => {
  let arr = str.split(',');
  return arr.reduce((a, v) => ({ ...a, [v.split(':')[0]]: v.split(':')[1] }), {});
};
