import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import QualityFilter from './Filter';
import QualityTable from './Table';
import FavoriteReports from '@jumbo/utils/FavoriteReports';

export default function QualityReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'survey.report'} />, isActive: true },
  ];
  const [data, setData] = useState([]);
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [totalCount, setTotalCount] = useState(10);

  return (
    <PageContainer
      heading={
        <>
          <IntlMessages id={'survey.report'} />
          <FavoriteReports reportId="22" />
        </>
      }
      breadcrumbs={breadcrumbs}>
      <QualityFilter setData={setData} setTotalCount={setTotalCount} limit={limit} offset={offset} />
      <QualityTable
        data={data}
        totalCount={totalCount}
        limit={limit}
        setLimit={setLimit}
        page={page}
        setPage={setPage}
        offset={offset}
        setOffset={setOffset}
      />
    </PageContainer>
  );
}
