import React, { useContext } from 'react';
import { Route, Redirect } from 'react-router-dom';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';

export const PrivateRouteLogin = ({ component: Component, ...rest }) => {
  const { firstPagePath } = useContext(AppContext);

  return (
    <Route
      {...rest}
      render={props =>
        parseInt(localStorage.getItem('login')) !== 200 && localStorage.getItem('validation') !== true ? (
          <Component {...props} />
        ) : (
          <Redirect to={{ pathname: firstPagePath, state: { from: props.location } }} />
        )
      }
    />
  );
};
