import React, { useContext } from 'react';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { questions } from './SurveyQuestions';
import { IntlProvider } from 'react-intl';
import AppLocale from '../../../../i18n';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';
export default function MailBodySurvey({ rawdata, score }) {
  const { locale } = useContext(AppContext);
  let arr = [];
  for (const [key, value] of Object.entries(rawdata)) {
    arr.push({
      question: questions[key].question,
      answer: value === '1' ? 'yes' : value === '0' ? 'no' : '-',
    });
  }
  return (
    <IntlProvider locale={AppLocale[locale.locale].locale} messages={AppLocale[locale.locale].messages}>
      <div>
        <h3><IntlMessages id={'survey'} /></h3>
        <ol>
          {arr.map((item, index) => (
            <li key={index}>
              <IntlMessages id={item.question} />
              <br />
              <strong>{item.answer !== '-' ? <IntlMessages id={item.answer} /> : '-'}</strong>
              <hr />
            </li>
          ))}
        </ol>
        <IntlMessages id={'surveyScore'} /> = <b>{score}</b>
      </div>
    </IntlProvider>
  );
}
