import { getDailySummaryModalRonaCall } from 'api/api';
import React, { useEffect, useState } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Button } from '@mui/material';
import IntlMessages from '@jumbo/utils/IntlMessages';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import CircularProgress from '@mui/material/CircularProgress';
export default function RonaCallModal({ date, handleRonaCallClose }) {
  let [data, setData] = useState([]);
  let [hideAgentToAgentCalls, setHideAgentToAgentCalls] = useState(false);
  let [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    getDailySummaryModalRonaCall(date, hideAgentToAgentCalls).then(response => {
      setData(JSON.parse(response.data.data));
      setLoading(false);
    });
  }, [date, hideAgentToAgentCalls]);
  return (
    <div>
      <FormControlLabel
        control={<Switch checked={hideAgentToAgentCalls} onChange={e => setHideAgentToAgentCalls(e.target.checked)} />}
        label={<IntlMessages id="hideInternalCalls" />}
      />
      <TableContainer style={{ height: '600px' }}>
        <Table stickyHeader>
          <TableHead>
            <TableRow>
              <TableCell>
                <b>#</b>
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.caller'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.called'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.starttime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.endtime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.ringtime'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <tr>
                <td colSpan="6" align="center">
                  <CircularProgress />
                </td>
              </tr>
            ) : (
              data.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>
                    <b>{index + 1}</b>
                  </TableCell>
                  <TableCell>{item.src_display_name}</TableCell>
                  <TableCell>{item.dst_display_name}</TableCell>
                  <TableCell>{item.start_time}</TableCell>
                  <TableCell>{item.end_time}</TableCell>
                  <TableCell>{item.ring_time}</TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </TableContainer>
      <div style={{ textAlign: 'right' }} lang="en">
        <Button style={{ backgroundColor: '#3f51b5', color: 'white' }} onClick={() => handleRonaCallClose()}>
          <IntlMessages id={'close'} />
        </Button>
      </div>
    </div>
  );
}
