import React, { useEffect, useState } from 'react';
import { getDailySummaryModalAnsweredCall } from 'api/api';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Button } from '@mui/material';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { getTotalSec } from '@jumbo/utils/dateHelper';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import CircularProgress from '@mui/material/CircularProgress';

export default function AnsweredCallModal({ date, handleAnsweredCallClose }) {
  let [data, setData] = useState([]);
  let [hideAgentToAgentCalls, setHideAgentToAgentCalls] = useState(false);
  let [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    getDailySummaryModalAnsweredCall(date, hideAgentToAgentCalls).then(result => {
      let tempResp = JSON.parse(result.data.data);
      let resp = [];
      let num = 0;
      for (let i = 0; i < tempResp.length; i++) {
        let { ...datas } = tempResp[i];
        if (i === 0) {
          resp.push({
            num: num + 1,
            otherCalls: [],
            ...datas,
          });
          num += 1;
        } else if (tempResp[i - 1].call_id === tempResp[i].call_id) {
          resp[num - 1].otherCalls.push(datas);
        } else {
          resp.push({
            num: num + 1,
            otherCalls: [],
            ...datas,
          });
          num += 1;
        }
      }
      setData(resp);
      setLoading(false);
    });
  }, [date, hideAgentToAgentCalls]);

  return (
    <div>
      <FormControlLabel
        control={<Switch checked={hideAgentToAgentCalls} onChange={e => setHideAgentToAgentCalls(e.target.checked)} />}
        label={<IntlMessages id="hideInternalCalls" />}
      />
      <TableContainer style={{ height: '600px' }}>
        <Table stickyHeader size="small">
          <TableHead>
            <TableRow>
              <TableCell />
              <TableCell>
                <b>#</b>
              </TableCell>
              <TableCell>
                <IntlMessages id={'callid'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.caller'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.called'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.starttime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.answertime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.endtime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.ringtime'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'dailysummary.modal.talktime'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {loading ? (
              <tr>
                <td colSpan="10" align="center">
                  <CircularProgress />
                </td>
              </tr>
            ) : (
              // data.map((item, index) => (
              //   <TableRow key={index}>
              //     <TableCell>
              //       <b>{item.num}</b>
              //     </TableCell>
              //     <TableCell>{item.src_display_name}</TableCell>
              //     <TableCell>{item.dst_display_name}</TableCell>
              //     <TableCell>{item.start_time}</TableCell>
              //     <TableCell>{item.answer_time}</TableCell>
              //     <TableCell>{getTotalSec(item.talk_time) === 0 ? item.start_time : item.end_time}</TableCell>
              //     <TableCell>{item.ring_time}</TableCell>
              //     <TableCell>{item.talk_time}</TableCell>
              //   </TableRow>
              // ))
              data.map((item, index) => <Row key={index} row={item} />)
            )}
          </TableBody>
        </Table>
      </TableContainer>
      <div style={{ textAlign: 'right' }} lang="en">
        <Button style={{ backgroundColor: '#3f51b5', color: 'white' }} onClick={() => handleAnsweredCallClose()}>
          <IntlMessages id={'close'} />
        </Button>
      </div>
    </div>
  );
}
function Row({ row }) {
  const [open, setOpen] = useState(false);

  return (
    <>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' } }}>
        <TableCell>
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
            color="info"
            disabled={row.otherCalls.length === 0}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
        <TableCell>
          <b>{row.num}</b>
        </TableCell>
        <TableCell>{row.call_id}</TableCell>
        <TableCell>{row.src_display_name}</TableCell>
        <TableCell>{row.dst_display_name}</TableCell>
        <TableCell>{row.start_time}</TableCell>
        <TableCell>{row.answer_time}</TableCell>
        <TableCell>{getTotalSec(row.talk_time) === 0 ? row.start_time : row.end_time}</TableCell>
        <TableCell>{row.ring_time}</TableCell>
        <TableCell>{row.talk_time}</TableCell>
      </TableRow>
      {row.otherCalls.length > 0 && (
        <TableRow>
          <TableCell style={{ padding: 0, borderBottom: 'none' }} colSpan={10}>
            <Collapse in={open} timeout="auto" unmountOnExit>
              <Table size="small" aria-label="details">
                <TableHead>
                  <TableRow>
                    <TableCell>
                      <b>#</b>
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'callid'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'dailysummary.modal.caller'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'dailysummary.modal.called'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'dailysummary.modal.starttime'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'dailysummary.modal.answertime'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'dailysummary.modal.endtime'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'dailysummary.modal.ringtime'} />
                    </TableCell>
                    <TableCell>
                      <IntlMessages id={'dailysummary.modal.talktime'} />
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {row.otherCalls.map((item, index) => (
                    <TableRow key={index}>
                      <TableCell>
                        <b>{index + 1}</b>
                      </TableCell>
                      <TableCell>{item.call_id}</TableCell>
                      <TableCell>{item.src_display_name}</TableCell>
                      <TableCell>{item.dst_display_name}</TableCell>
                      <TableCell>{item.start_time}</TableCell>
                      <TableCell>{item.answer_time}</TableCell>
                      <TableCell>{getTotalSec(item.talk_time) === 0 ? item.start_time : item.end_time}</TableCell>
                      <TableCell>{item.ring_time}</TableCell>
                      <TableCell>{item.talk_time}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </Collapse>
          </TableCell>
        </TableRow>
      )}
    </>
  );
}
