import React, { useEffect, useState } from 'react';
import Rating from '@mui/material/Rating';
import Tooltip from '@mui/material/Tooltip';
import IntlMessages from '@jumbo/utils/IntlMessages';

export default function FavoriteReports({ reportId }) {
  let [favorite, setFavorite] = useState(0);
  useEffect(() => {
    let storage = localStorage.getItem('favs') || '';
    if (storage !== '' && storage.split(',').includes(reportId)) {
      setFavorite(1);
    }
  }, [reportId]);
  const handleFavoriteChange = (e, newValue) => {
    let storage = localStorage.getItem('favs') || '';
    let tempArr = [];
    if (storage !== '') {
      tempArr = storage.split(',');
    }
    if (newValue === 1) {
      tempArr.push(reportId);
      localStorage.setItem('favs', tempArr.join(','));
      window.dispatchEvent(new Event('storage'));
    } else {
      tempArr.splice(tempArr.indexOf(reportId), 1);
      localStorage.setItem('favs', tempArr.join(','));
      window.dispatchEvent(new Event('storage'));
    }
    setFavorite(newValue);
  };
  return (
    <Tooltip
      title={favorite === 1 ? <IntlMessages id="removeFromFavorites" /> : <IntlMessages id="addToFavorites" />}
      placement="right"
      arrow
      enterDelay={500}
      leaveDelay={200}>
      <Rating sx={{ ml: 1 }} max={1} name="Add Report to Favorites" value={favorite} onChange={handleFavoriteChange} />
    </Tooltip>
  );
}
