import React, { useEffect, useState } from 'react';
import { getAgentNumber, getQualityReport, getQualityReportCount } from 'api/api';
import QualityReportDateFilter from './DateFilter';
import QualityReportFilters from './Filters';
import dateFormat from 'dateformat';

export default function QualityFilters({ setData, setTotalCount, limit, offset }) {
  const [agents, setAgents] = useState([]);
  const [agentNo, setAgentNo] = useState('');
  const [agentName, setAgentName] = useState([]);
  const [score, setScore] = useState('');

  const [startTimeApi, setStartTimeApi] = useState(dateFormat(new Date().setHours(0, 0, 0), 'yyyy-mm-dd HH:MM:ss'));
  const [endTimeApi, setEndTimeApi] = useState(dateFormat(new Date().setHours(23, 59, 59), 'yyyy-mm-dd HH:MM:ss'));
  useState(() => {
    getAgentNumber().then(resp => setAgents(JSON.parse(resp.data.data)));
  }, []);

  useEffect(() => {
    getQualityReport(startTimeApi, endTimeApi, agentNo, agentName, score, limit, offset).then(resp => {
      setData(JSON.parse(resp.data.data));
    });
    getQualityReportCount(startTimeApi, endTimeApi, agentNo, agentName, score).then(resp => {
      setTotalCount(parseInt(JSON.parse(resp.data.data)[0].count));
    });
  }, [startTimeApi, endTimeApi, agentNo, agentName, score, limit, offset, setData, setTotalCount]);
  return (
    <div style={{ margin: '20px 0' }}>
      <QualityReportDateFilter setStartTimeApi={setStartTimeApi} setEndTimeApi={setEndTimeApi} />
      <QualityReportFilters
        agents={agents}
        agentNo={agentNo}
        setAgentNo={setAgentNo}
        agentName={agentName}
        setAgentName={setAgentName}
        score={score}
        setScore={setScore}
      />
    </div>
  );
}
