import { Box, Button, Modal, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import React, { useEffect, useState } from 'react';
import AddIcon from '@mui/icons-material/Add';
import AddRoleModal from './AddRoleModal';
import UpdateRoleModal from './UpdateRoleModal';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import IntlMessages from '@jumbo/utils/IntlMessages';
import IconButton from '@mui/material/IconButton';
import { deleteRoleByRolename, getRoles } from 'api/api';
export default function RoleTable({ reloadValues, setReloadValues }) {
  let [roles, setRoles] = useState([]);
  useEffect(() => {
    getRoles().then(result => {
      setRoles(JSON.parse(result.data.data));
    });
  }, [reloadValues]);

  const [addModal, setAddModal] = useState(false);
  const addHandleOpen = () => setAddModal(true);
  const addHandleClose = () => setAddModal(false);

  const [updateModal, setUpdateModal] = useState(false);
  const [selectedRole, setSelectedRole] = useState({ rolename: '', accessablepages: '' });
  const updateHandleOpen = () => setUpdateModal(true);
  const updateHandleClose = () => setUpdateModal(false);
  const handleClickEdit = role => {
    setSelectedRole(role);
    updateHandleOpen();
  };

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: '60%',
    bgcolor: 'background.paper',
    boxShadow: 24,
    p: 4,
    borderRadius: '8px',
    maxHeight: '90vh',
    overflowY: 'scroll',
  };

  const handleDeleteClick = rolename => {
    deleteRoleByRolename(rolename).then(() => {
      setReloadValues(new Date().getTime());
    });
  };

  return (
    <>
      <div style={{ marginBottom: '10px', textAlign: 'right' }}>
        <Button sx={{ backgroundColor: '#3E7C17', color: 'white' }} onClick={() => addHandleOpen()}>
          <AddIcon sx={{ verticalAlign: '-5px' }} /> <IntlMessages id={'addrole'} />
        </Button>
      </div>
      <TableContainer component={Paper}>
        <Table sx={{ maxHeight: '500px' }}>
          <TableHead>
            <TableRow>
              <TableCell align="left">
                <b>#</b>
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'rolename'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'edit'} />
              </TableCell>
              <TableCell align="center">
                <IntlMessages id={'delete'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            <TableRow>
              <TableCell align="left">
                <b>1</b>
              </TableCell>
              <TableCell align="center">
                <b>Admin</b>
              </TableCell>
              <TableCell align="center">
                <IconButton color="warning" disabled>
                  <EditIcon />
                </IconButton>
              </TableCell>
              <TableCell align="center">
                <IconButton color="error" disabled>
                  <DeleteIcon />
                </IconButton>
              </TableCell>
            </TableRow>
            {roles.map((item, index) => (
              <TableRow key={index}>
                <TableCell align="left">{index + 2}</TableCell>
                <TableCell align="center">
                  <b>{item.rolename}</b>
                </TableCell>
                <TableCell align="center">
                  <IconButton color="warning" onClick={() => handleClickEdit(item)}>
                    <EditIcon />
                  </IconButton>
                </TableCell>
                <TableCell align="center">
                  <IconButton color="error" onClick={() => handleDeleteClick(item.rolename)}>
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Modal open={addModal} onClose={addHandleClose} aria-labelledby="add-role-modal" aria-describedby="add-role-modal">
        <Box sx={style}>
          <AddRoleModal roles={roles} addHandleClose={addHandleClose} setReloadValues={setReloadValues} />
        </Box>
      </Modal>
      <Modal
        open={updateModal}
        onClose={updateHandleClose}
        aria-labelledby="update-role-modal"
        aria-describedby="update-role-modal">
        <Box sx={style}>
          <UpdateRoleModal
            roles={roles}
            selectedRole={selectedRole}
            setReloadValues={setReloadValues}
            updateHandleClose={updateHandleClose}
          />
        </Box>
      </Modal>
    </>
  );
}
