import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import DailySummaryReportFilter from './Filter';
import DailySummaryReportTable from './Table';
import FavoriteReports from '@jumbo/utils/FavoriteReports';

export default function DailySumaryReport() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'dailysummary'} />, isActive: true },
  ];

  let [data, setData] = useState([]);
  let [dataTotal, setDataTotal] = useState([]);

  return (
    <div>
      <PageContainer
        heading={
          <>
            <IntlMessages id={'dailysummary'} />
            <FavoriteReports reportId="7" />
          </>
        }
        breadcrumbs={breadcrumbs}>
        <DailySummaryReportFilter setData={setData} setDataTotal={setDataTotal} />
        <DailySummaryReportTable data={data} dataTotal={dataTotal} />
      </PageContainer>
    </div>
  );
}
