import React, { useState, useEffect } from 'react';
import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { styled } from '@mui/material/styles';
// import { sendCampaigns } from 'api/api';
import { useFormik } from 'formik';
import { DateTimePicker } from '@mui/x-date-pickers';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { TextField, MenuItem, FormControl, InputLabel, Select, Button, Typography } from '@mui/material';
import SaveIcon from '@mui/icons-material/Save';
import LoadingButton from '@mui/lab/LoadingButton';
import deLocale from 'date-fns/locale/de';
import nlLocale from 'date-fns/locale/nl';
import trLocale from 'date-fns/locale/tr';
import enLocale from 'date-fns/locale/en-US';
import { useIntl } from 'react-intl';
import GridContainer from '@jumbo/components/GridContainer';
import Grid from '@mui/material/Grid';
import { TimePicker } from '@mui/x-date-pickers/TimePicker';
import { getQueue } from 'api/api';

export default function Campaigns() {
  const breadcrumbs = [
    { label: <IntlMessages id={'reports.callcenter'} />, link: '/' },
    { label: <IntlMessages id={'campaigns'} />, isActive: true },
  ];
  const Input = styled('input')({
    display: 'none',
  });
  const [btnLoading, setBtnLoading] = useState(false);
  const [scheduleBtnLoading, setScheduleBtnLoading] = useState(false);
  let curentLanguage = useIntl();

  const [queuesList, setQueuesList] = useState([]);
  useEffect(() => {
    getQueue().then(response => {
      setQueuesList(JSON.parse(response.data.data));
    });
  }, []);

  const formik = useFormik({
    initialValues: {
      startDate: new Date(),
      endDate: new Date(),
      callType: '',
      campaignName: '',
      csvfile: '',
      wavfile: '',
      ringtime: new Date().setMinutes(1, 0),
      channelCount: 1,
      anons: '',
      ivrMenu: '',
      repeatCallsCount: 1,
      queue: '',
    },
    onSubmit: values => {
      setBtnLoading(true);
      console.log('values', values);
      setTimeout(() => {
        setBtnLoading(false);
      }, 1000);
    },
  });
  const schedule = useFormik({
    initialValues: {
      success: new Date().setMinutes(1, 0),
      unsuccess: new Date().setMinutes(1, 0),
      sended: new Date().setMinutes(1, 0),
      notSended: new Date().setMinutes(1, 0),
      callAgain: new Date().setMinutes(1, 0),
      unanswered: new Date().setMinutes(1, 0),
    },
    onSubmit: values => {
      setScheduleBtnLoading(true);
      console.log('schedule', values);
      setTimeout(() => {
        setScheduleBtnLoading(false);
      }, 1000);
    },
  });
  return (
    <PageContainer heading={<IntlMessages id={'campaigns'} />} breadcrumbs={breadcrumbs}>
      <div>
        <form onSubmit={formik.handleSubmit}>
          <GridContainer>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <DateTimePicker
                  label={<IntlMessages id={'filter.menu.startdate'} />}
                  name="startDate"
                  value={formik.values.startDate}
                  inputFormat="dd/MM/yyyy HH:mm"
                  maxDateTime={formik.values.endDate}
                  onChange={val => formik.setFieldValue('startDate', val)}
                  renderInput={params => <TextField fullWidth helperText="dd/MM/yyyy hh:mm" {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <DateTimePicker
                  label={<IntlMessages id={'filter.menu.enddate'} />}
                  value={formik.values.endDate}
                  minDateTime={formik.values.startDate}
                  inputFormat="dd/MM/yyyy HH:mm"
                  onChange={val => formik.setFieldValue('endDate', val)}
                  name="endDate"
                  renderInput={params => <TextField fullWidth helperText="dd/MM/yyyy hh:mm" {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <FormControl fullWidth>
                <InputLabel id="callType">
                  <IntlMessages id={'callType'} />
                </InputLabel>
                <Select
                  labelId="callType"
                  name="callType"
                  value={formik.values.callType}
                  label={<IntlMessages id={'callType'} />}
                  onChange={formik.handleChange}>
                  <MenuItem value="inb">
                    <IntlMessages id={'incomingcall'} />
                  </MenuItem>
                  <MenuItem value="out">
                    <IntlMessages id={'outgoingcall'} />
                  </MenuItem>
                  <MenuItem value="agent">
                    <IntlMessages id={'agentedcall'} />
                  </MenuItem>
                  <MenuItem value="ques">
                    <IntlMessages id={'queue'} />
                  </MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={6} sm={12}>
              <TextField
                fullWidth
                label={<IntlMessages id={'campaignName'} />}
                name="campaignName"
                value={formik.values.campaignName}
                onChange={formik.handleChange}
              />
            </Grid>
            <Grid item md={6} sm={12}>
              <label htmlFor="csvupload">
                <Input
                  accept=".csv"
                  id="csvupload"
                  type="file"
                  onChange={val => formik.setFieldValue('csvfile', val.target.files[0])}
                />
                <Button fullWidth variant="outlined" color="secondary" component="span">
                  Upload CSV
                </Button>
              </label>
            </Grid>
            <Grid item md={6} sm={12}>
              <label htmlFor="wavupload">
                <Input
                  accept="audio/wav"
                  id="wavupload"
                  type="file"
                  onChange={val => formik.setFieldValue('wavfile', val.target.files[0])}
                />
                <Button fullWidth variant="outlined" color="success" component="span">
                  Upload WAV
                </Button>
              </label>
            </Grid>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <TimePicker
                  label={<IntlMessages id={'CallLog.datail.ringtime'} />}
                  value={formik.values.ringtime}
                  views={['minutes', 'seconds']}
                  inputFormat="mm:ss"
                  mask="__:__"
                  onChange={val => formik.setFieldValue('ringtime', val)}
                  name="ringtime"
                  renderInput={params => <TextField fullWidth {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <TextField
                fullWidth
                label={<IntlMessages id={'channelCount'} />}
                name="channelCount"
                type="number"
                value={formik.values.channelCount}
                onChange={formik.handleChange}
              />
            </Grid>
            <Grid item md={6} sm={12}>
              <TextField
                fullWidth
                label={<IntlMessages id={'announcement'} />}
                name="anons"
                value={formik.values.anons}
                onChange={formik.handleChange}
              />
            </Grid>
            <Grid item md={6} sm={12}>
              <TextField
                fullWidth
                label={<IntlMessages id={'IVRMenu'} />}
                name="ivrMenu"
                value={formik.values.ivrMenu}
                onChange={formik.handleChange}
              />
            </Grid>
            <Grid item md={6} sm={12}>
              <TextField
                fullWidth
                label={<IntlMessages id={'repeatCallsCount'} />}
                name="repeatCallsCount"
                type="number"
                value={formik.values.repeatCallsCount}
                onChange={formik.handleChange}
              />
            </Grid>
            <Grid item md={6} sm={12}>
              <FormControl fullWidth>
                <InputLabel id="queue">
                  <IntlMessages id={'queue'} />
                </InputLabel>
                <Select
                  labelId="queue"
                  name="queue"
                  value={formik.values.queue}
                  label={<IntlMessages id={'queue'} />}
                  onChange={formik.handleChange}>
                  {queuesList.length > 0 &&
                    queuesList.map((item, index) => (
                      <MenuItem key={index} value={item.display_name}>
                        {item.dn} - {item.display_name}
                      </MenuItem>
                    ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item md={12}>
              <LoadingButton
                size="large"
                fullWidth
                type="submit"
                variant="contained"
                loading={btnLoading}
                loadingPosition="start"
                startIcon={<SaveIcon />}
                sx={{ mt: 1 }}>
                <IntlMessages id={'save'} />
              </LoadingButton>
            </Grid>
          </GridContainer>
        </form>
      </div>
      <div style={{ marginTop: '2rem' }}>
        <Typography variant="h5" component="div" mb={3} sx={{ textTransform: 'uppercase' }}>
          <IntlMessages id={'schedule'} />
          <hr />
        </Typography>
        <form onSubmit={schedule.handleSubmit}>
          <GridContainer>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <TimePicker
                  label={<IntlMessages id={'success'} />}
                  value={schedule.values.success}
                  views={['minutes', 'seconds']}
                  inputFormat="mm:ss"
                  mask="__:__"
                  onChange={val => schedule.setFieldValue('success', val)}
                  name="success"
                  renderInput={params => <TextField fullWidth {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <TimePicker
                  label={<IntlMessages id={'unsuccess'} />}
                  value={schedule.values.unsuccess}
                  views={['minutes', 'seconds']}
                  inputFormat="mm:ss"
                  mask="__:__"
                  onChange={val => schedule.setFieldValue('unsuccess', val)}
                  name="unsuccess"
                  renderInput={params => <TextField fullWidth {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <TimePicker
                  label={<IntlMessages id={'sended'} />}
                  value={schedule.values.sended}
                  views={['minutes', 'seconds']}
                  inputFormat="mm:ss"
                  mask="__:__"
                  onChange={val => schedule.setFieldValue('sended', val)}
                  name="sended"
                  renderInput={params => <TextField fullWidth {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <TimePicker
                  label={<IntlMessages id={'notSended'} />}
                  value={schedule.values.notSended}
                  views={['minutes', 'seconds']}
                  inputFormat="mm:ss"
                  mask="__:__"
                  onChange={val => schedule.setFieldValue('notSended', val)}
                  name="notSended"
                  renderInput={params => <TextField fullWidth {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <TimePicker
                  label={<IntlMessages id={'callAgain'} />}
                  value={schedule.values.callAgain}
                  views={['minutes', 'seconds']}
                  inputFormat="mm:ss"
                  mask="__:__"
                  onChange={val => schedule.setFieldValue('callAgain', val)}
                  name="callAgain"
                  renderInput={params => <TextField fullWidth {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={6} sm={12}>
              <LocalizationProvider
                dateAdapter={AdapterDateFns}
                adapterLocale={
                  curentLanguage.locale === 'en-EN'
                    ? enLocale
                    : curentLanguage.locale === 'nl-NL'
                    ? nlLocale
                    : curentLanguage.locale === 'tr-TR'
                    ? trLocale
                    : curentLanguage.locale === 'de-DE'
                    ? deLocale
                    : enLocale
                }>
                <TimePicker
                  label={<IntlMessages id={'notanswered'} />}
                  value={schedule.values.unanswered}
                  views={['minutes', 'seconds']}
                  inputFormat="mm:ss"
                  mask="__:__"
                  onChange={val => schedule.setFieldValue('unanswered', val)}
                  name="unanswered"
                  renderInput={params => <TextField fullWidth {...params} />}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item md={12}>
              <LoadingButton
                size="large"
                fullWidth
                type="submit"
                variant="contained"
                loading={scheduleBtnLoading}
                loadingPosition="start"
                startIcon={<SaveIcon />}
                sx={{ mt: 1 }}>
                <IntlMessages id={'save'} />
              </LoadingButton>
            </Grid>
          </GridContainer>
        </form>
      </div>
    </PageContainer>
  );
}
