import IntlMessages from '@jumbo/utils/IntlMessages';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  TablePagination,
  Button,
} from '@material-ui/core';
import { lighten, makeStyles } from '@material-ui/core/styles';
import React, { useEffect, useState } from 'react';
import dateFormat from 'dateformat';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Chip from '@mui/material/Chip';
import { useIntl } from 'react-intl';
import ExcelFile from 'react-export-excel/dist/ExcelPlugin/components/ExcelFile';
import ExcelColumn from 'react-export-excel/dist/ExcelPlugin/elements/ExcelColumn';
import ExcelSheet from 'react-export-excel/dist/ExcelPlugin/elements/ExcelSheet';
import { CloudDownload } from '@material-ui/icons';
export default function QualityTable({ data, totalCount, limit, setLimit, page, setPage, offset, setOffset }) {
  var nonRepeatingArr = [];
  var copyArr = JSON.parse(JSON.stringify(data));
  for (var i = 0; i < data.length; i++) {
    var temp = [];
    for (var j = 0; j < copyArr.length; j++) {
      if (data[i].agent === copyArr[j].agent && data[i].score === copyArr[j].score) {
        temp.push(data[i].ani);
        copyArr[j].score = null;
      }
    }
    if (temp.length > 0) {
      nonRepeatingArr.push({
        agent: data[i].agent,
        score: data[i].score,
        timeofinsertion: data[i].timeofinsertion,
        voters: temp,
      });
    }
  }

  const [averageScores, setAverageScores] = useState([]);
  useEffect(() => {
    const totalScoreByAgent = data.reduce((acc, item) => {
      return { ...acc, [item.agent]: (acc[item.agent] || 0) + parseInt(item.score) };
    }, {});
    const totalVotesByAgent = data.reduce((acc, item) => {
      return { ...acc, [item.agent]: (acc[item.agent] || 0) + 1 };
    }, {});
    let temp = [];
    for (let i in totalScoreByAgent) {
      temp.push({
        agent: i,
        totalVote: totalVotesByAgent[i],
        averageScore: (totalScoreByAgent[i] / totalVotesByAgent[i]).toFixed(1),
      });
    }
    setAverageScores(temp);
  }, [data]);

  const [voters, setVoters] = useState([]);
  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };

  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 440,
    },
  }));
  const classes = useStyles();
  let curentLanguage = useIntl();

  return (
    <>
      {data.length > 0 && (
        <div style={{ marginTop: '10px', textAlign: 'right' }}>
          <ExcelFile
            filename={
              curentLanguage.locale === 'en-EN'
                ? 'Customer_Survey_Report'
                : curentLanguage.locale === 'tr-TR'
                ? 'Musteri_Anket_Raporu'
                : curentLanguage.locale === 'nl-NL'
                ? 'Onderzoeksrapport'
                : curentLanguage.locale === 'de-DE'
                ? 'Kundenbefragungsbericht'
                : ''
            }
            element={
              <Button style={{ backgroundColor: '#3f51b5', color: 'white' }}>
                <IntlMessages id={'download'} /> <CloudDownload style={{ marginLeft: '5px' }} />
              </Button>
            }>
            <ExcelSheet data={data} name="data">
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Date'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Tarih'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Datum'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Datum'
                    : ''
                }
                value="timeofinsertion"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Agent Number'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Agent Numarasƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Agent Nummer'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Agentennummer'
                    : ''
                }
                value="agent"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Customer Number'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'M√º≈üteri Numarasƒ±'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Klantnummer'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Kundennummer'
                    : ''
                }
                value="ani"
              />
              <ExcelColumn
                label={
                  curentLanguage.locale === 'en-EN'
                    ? 'Score'
                    : curentLanguage.locale === 'tr-TR'
                    ? 'Puan'
                    : curentLanguage.locale === 'nl-NL'
                    ? 'Scoren'
                    : curentLanguage.locale === 'de-DE'
                    ? 'Ergebnis'
                    : ''
                }
                value="score"
              />
            </ExcelSheet>
          </ExcelFile>
        </div>
      )}
      <Paper className={classes.root}>
        <TableContainer className={classes.container}>
          <Table stickyHeader aria-label="Scores" size="small">
            <TableHead>
              <TableRow>
                <TableCell>
                  <b>#</b>
                </TableCell>
                <TableCell>
                  <IntlMessages id={'date'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'agent.name'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'votescount'} />
                </TableCell>
                <TableCell>
                  <IntlMessages id={'score'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {nonRepeatingArr.length > 0 &&
                nonRepeatingArr.map((item, index) => (
                  <TableRow key={index}>
                    <TableCell>{index + 1 + offset}</TableCell>
                    <TableCell>{dateFormat(item.timeofinsertion, 'dd/mm/yyyy HH:MM:ss')}</TableCell>
                    <TableCell>{item.agent}</TableCell>
                    <TableCell>
                      <span
                        aria-controls={open ? 'voters-menu' : undefined}
                        aria-haspopup="true"
                        style={{ cursor: 'pointer' }}
                        onClick={e => {
                          setVoters(item.voters);
                          setAnchorEl(e.currentTarget);
                        }}>
                        <b>{item.voters.length}</b>
                      </span>
                    </TableCell>
                    <TableCell>{item.score}</TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[10, 25, 50, 100]}
          component="div"
          count={totalCount}
          backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
          nextIconButtonProps={data.length < limit || data.length === 0 ? { disabled: true } : { disabled: false }}
          rowsPerPage={limit}
          page={page}
          onPageChange={handleChangePage}
          labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>

      {averageScores.length > 0 && (
        <TableContainer style={{ maxHeight: '500px', marginTop: '1rem', width: '500px', float: 'right' }} component={Paper}>
          <Table stickyHeader aria-label="Average Scores">
            <TableHead>
              <TableRow>
                <TableCell align="right">
                  <IntlMessages id={'agent.name'} />
                </TableCell>
                <TableCell align="right">
                  <IntlMessages id={'totalvotes'} />
                </TableCell>
                <TableCell align="right">
                  <IntlMessages id={'average'} />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {averageScores.map((item, index) => (
                <TableRow key={index}>
                  <TableCell align="right">{item.agent}</TableCell>
                  <TableCell align="right">{item.totalVote}</TableCell>
                  <TableCell align="right">
                    <b>{item.averageScore}</b>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}

      <Menu id="voters-menu" anchorEl={anchorEl} open={open} onClose={handleClose}>
        {voters.map((item, index) => (
          <MenuItem key={index}>
            <Chip label={item} variant="outlined" />
          </MenuItem>
        ))}
      </Menu>
    </>
  );
}
