import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import { Button, TextField, FormControlLabel, Checkbox, Typography } from '@mui/material';
import React, { useState } from 'react';
import { NotificationContainer, NotificationManager } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { addRole } from 'api/api';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { CheckboxDatas } from 'routes';
export default function AddRoleModal({ roles, setReloadValues, addHandleClose }) {
  let [roleName, setRoleName] = useState('');
  let [allowedPages, setAllowedPages] = useState([]);
  const handleSubmit = () => {
    if (roleName === undefined || roleName === '') {
      NotificationManager.error('Rol adƒ± doƒüru formatta deƒüil', '', 3000);
    } else if (allowedPages.length === 0) {
      NotificationManager.error('En az 1 sayfa se√ßiniz', '', 3000);
    } else if (roles.some(e => e.rolename === roleName)) {
      NotificationManager.error('Bu isimde rol zaten mevcut', '', 3000);
    } else {
      addRole(roleName, allowedPages.join(',')).then(() => {
        setReloadValues(new Date().getTime());
        addHandleClose();
      });
    }
  };

  const handleChecked = (e, id) => {
    if (e.target.checked) {
      setAllowedPages(prev => [...prev, id]);
    } else {
      let tempArr = JSON.parse(JSON.stringify(allowedPages));
      tempArr.splice(tempArr.indexOf(id), 1);
      setAllowedPages(tempArr);
    }
  };
  const handleSelectAll = e => {
    if (e.target.checked) {
      let tempArr = [];
      for (let i = 0; i < 31; i++) {
        tempArr.push(i.toString());
      }
      setAllowedPages(tempArr);
    } else {
      setAllowedPages([]);
    }
  };
  return (
    <div>
      <NotificationContainer />
      <GridContainer>
        <Grid item md={12}>
          <TextField
            fullWidth
            variant="outlined"
            id="roleName"
            type="text"
            name="roleName"
            value={roleName}
            label={<IntlMessages id={'rolename'} />}
            onChange={e => setRoleName(e.target.value)}
          />
        </Grid>
        <Grid item md={12}>
          <Grid container spacing={3} alignItems="flex-start">
            <Grid item md={12}>
              <Grid container spacing={0} alignItems="center" justifyContent="space-between">
                <Grid item>
                  <Typography variant="h5" gutterBottom component="div">
                    <IntlMessages id={'allowedPages'} />
                  </Typography>
                </Grid>
                <Grid item>
                  <FormControlLabel
                    control={<Checkbox checked={allowedPages.length === 31} onChange={handleSelectAll} />}
                    label={<IntlMessages id={'all'} />}
                    labelPlacement="start"
                  />
                </Grid>
              </Grid>

              <hr />
            </Grid>
            {CheckboxDatas.map((item, index) => (
              <Grid item md={4} key={index}>
                <FormControlLabel
                  control={<Checkbox checked={allowedPages.includes(item.id)} onChange={e => handleChecked(e, item.id)} />}
                  label={<IntlMessages id={item.name} />}
                />
              </Grid>
            ))}
          </Grid>
        </Grid>
        <Grid item md={12}>
          <Button fullWidth color="info" variant="contained" onClick={handleSubmit}>
            <IntlMessages id={'add'} />
          </Button>
        </Grid>
      </GridContainer>
    </div>
  );
}
