import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import AuditLogFilter from './Filter';
import AuditLogTable from './Table';

export default function AuditLog() {
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'auditLog'} />, isActive: true },
  ];
  const [data, setData] = useState([]);
  const [limit, setLimit] = useState(10);
  const [offset, setOffset] = useState(0);
  const [page, setPage] = useState(0);
  const [totalCount, setTotalCount] = useState(10);

  return (
    <PageContainer heading={<IntlMessages id={'auditLog'} />} breadcrumbs={breadcrumbs}>
      <AuditLogFilter setData={setData} setTotalCount={setTotalCount} limit={limit} offset={offset} />
      <AuditLogTable
        data={data}
        totalCount={totalCount}
        limit={limit}
        setLimit={setLimit}
        page={page}
        setPage={setPage}
        offset={offset}
        setOffset={setOffset}
      />
    </PageContainer>
  );
}
