import defaultTheme from '../../../../theme/defaultTheme';
import {
  DRAWER_BREAK_POINT,
  HEADER_TYPE,
  LAYOUT_STYLES,
  LAYOUT_TYPES,
  SIDEBAR_TYPE,
  SIDEBAR_WIDTH,
  THEME_TYPES,
} from '../../../constants/ThemeOptions';
const getLang = () => {
  var defaultLanguage = localStorage.getItem('language') || navigator.language;
  switch (defaultLanguage) {
    case 'tr-TR':
    case 'tr':
    case 'turkish':
      return {
        languageId: 'turkish',
        locale: 'tr',
        name: 'T√ºrk√ße',
        icon: 'tr',
      };
    case 'en-US':
    case 'en':
    case 'en-GB':
    case 'english':
      return {
        languageId: 'english',
        locale: 'en',
        name: 'English',
        icon: 'en',
      };
    case 'de-DE':
    case 'de':
    case 'de-AT':
    case 'de-CH':
    case 'de-LI':
    case 'deutsche':
      return {
        languageId: 'deutsche',
        locale: 'de',
        name: 'Deutsche',
        icon: 'de',
      };
    case 'nl':
    case 'dutch':
      return {
        languageId: 'dutch',
        locale: 'nl',
        name: 'Dutch',
        icon: 'nl',
      };
    default:
      return {
        languageId: 'turkish',
        locale: 'tr',
        name: 'Turkish',
        icon: 'tr',
      };
  }
};
var defaultLng = getLang();
export default {
  theme: defaultTheme,
  defaultLng,
  layout: LAYOUT_TYPES.VERTICAL_MODERN_SIDEBAR,
  layoutType: LAYOUT_STYLES.FULL_WIDTH,
  themeType: THEME_TYPES.LIGHT,
  drawerBreakPoint: DRAWER_BREAK_POINT.MD,
  headerType: HEADER_TYPE.FIXED,
  sidebarType: SIDEBAR_TYPE.FULL,
  isSidebarFixed: true,
  sidebarWidth: SIDEBAR_WIDTH.DEFAULT,
  showFooter: false,
};
