const OptionalPages = [
  { id: '0', path: '/dashboard', component: Dashboard },
  { id: '1', path: '/wallboard', component: WallBoard },
  { id: '2', path: '/reports/agentstatus', component: AgentStatus },
  { id: '3', path: '/reports/campaigns', component: Campaigns },
  { id: '4', path: '/reports/callLog', component: CallLog },
  { id: '5', path: '/reports/calllistening', component: CallListening },
  { id: '6', path: '/reports/callmanagement', component: CallManagement },
  { id: '7', path: '/reports/calltransferreport', component: CallTransferReport },
  { id: '8', path: '/reports/internalssummary', component: InternalsSummaryReport },
  { id: '9', path: '/reports/didreport', component: DidReport },
  { id: '10', path: '/reports/dailysummaryreport', component: DailySumaryReport },
  { id: '11', path: '/reports/queueanswerreport', component: QueueAnswerReport },
  { id: '12', path: '/reports/abandoned-queues', component: AbandonedQueues },
  { id: '13', path: '/reports/droppedcall', component: DroppedCall },
  { id: '14', path: '/reports/abondenedcalls', component: AbondenedCalls },
  { id: '15', path: '/reports/calltermination', component: CallTerminationReport },
  { id: '16', path: '/reports/repetetive-calls', component: RepetetiveCalls },
  { id: '17', path: '/reports/agentcdr', component: AgentCDR },
  { id: '18', path: '/reports/servicelevel', component: ServiceLevel },
  { id: '19', path: '/reports/dailylog', component: DailyLog },
  { id: '20', path: '/reports/dailyacc', component: DailyAcc },
  { id: '21', path: '/reports/callcomparison', component: CallComparison },
  { id: '22', path: '/reports/monthlylog', component: MonthlyLog },
  { id: '23', path: '/reports/monthlyDetails', component: MonthlyDetails },
  { id: '24', path: '/reports/quality-report', component: QualityReport },
  { id: '25', path: '/settings/editbreaks', component: EditBreaks },
  { id: '26', path: '/settings/departmentsettings', component: DepartmentSettings },
  { id: '27', path: '/settings/usersettings', component: UserSettings },
  { id: '28', path: '/settings/rolesettings', component: RoleSettings },
  // { id: '29', path: '/iys/called-number', component: IYS },
  // { id: '30', path: '/teams/users', component: Users },
];
const Routes = () => {
  const location = useLocation();
  const [filteredPages, setFilteredPages] = useState(OptionalPages);
  const { rolePages, firstPagePath } = useContext(AppContext);

  useEffect(() => {
    setAuditLog(location.pathname);
  }, [location.pathname]);
  useEffect(() => {
    let allowedPagesArr = rolePages.split(',');
    let tempArr = OptionalPages.filter(e => allowedPagesArr.includes(e.id));
    setFilteredPages(tempArr);
  }, [rolePages]);
  if (location.pathname === '' || location.pathname === '/') {
    return <Redirect to={firstPagePath} />;
  }
  return (
    <React.Fragment>
      <Switch>
        <NotPrivateRoute path="/signup" component={Register} />
        <PrivateRouteLogin path="/signin" component={Login} />
        <NotPrivateRoute path="/forgot-password" component={ForgotPasswordPage} />
        <NotPrivateRoute path="/account" component={Account} />
        {filteredPages.map((item, index) => (
          <NotPrivateRoute key={index} path={item.path} component={item.component} />
        ))}

        {/* <NotPrivateRoute path="/reports/returned-dropcall" component={ReturnedDropCall} /> */}
        <NotPrivateRoute path="/auditLog" component={AuditLog} />
        <Route component={Error404} />
      </Switch>
    </React.Fragment>
  );
};

export default Routes;
