import React, { useEffect, useState } from 'react';
import { getAuditLog, getAuditLogCount } from 'api/api';
import AuditLogDateFilter from './DateFilter';
import AuditLogFilters from './Filters';
import dateFormat from 'dateformat';

export default function AuditLogFiter({ setData, setTotalCount, limit, offset }) {
  const [username, setUsername] = useState('');
  const [startTimeApi, setStartTimeApi] = useState(dateFormat(new Date().setHours(0, 0, 0), 'yyyy-mm-dd HH:MM:ss'));
  const [endTimeApi, setEndTimeApi] = useState(dateFormat(new Date().setHours(23, 59, 59), 'yyyy-mm-dd HH:MM:ss'));
  useEffect(() => {
    getAuditLog(startTimeApi, endTimeApi, limit, offset, username).then(resp => {
      setData(JSON.parse(resp.data.data));
    });
    getAuditLogCount(startTimeApi, endTimeApi, username).then(resp => {
      setTotalCount(parseInt(JSON.parse(resp.data.data)[0].count));
    });
  }, [startTimeApi, endTimeApi, limit, offset, setData, setTotalCount, username]);
  return (
    <div style={{ margin: '20px 0' }}>
      <AuditLogDateFilter setStartTimeApi={setStartTimeApi} setEndTimeApi={setEndTimeApi} />
      <AuditLogFilters username={username} setUsername={setUsername} />
    </div>
  );
}
