import PageContainer from '@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import React, { useState } from 'react';
import RoleTable from './RoleTable';

export default function RoleSettings() {
  const breadcrumbs = [
    { label: <IntlMessages id={'settings'} />, link: '/' },
    { label: <IntlMessages id={'rolesettings'} />, isActive: true },
  ];
  let [reloadValues, setReloadValues] = useState('');

  return (
    <div>
      <PageContainer heading={<IntlMessages id={'rolesettings'} />} breadcrumbs={breadcrumbs}>
        <RoleTable reloadValues={reloadValues} setReloadValues={setReloadValues} />
      </PageContainer>
    </div>
  );
}
