import IntlMessages from '@jumbo/utils/IntlMessages';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TablePagination } from '@material-ui/core';
import { lighten, makeStyles } from '@material-ui/core/styles';
import React from 'react';
import dateFormat from 'dateformat';
export default function AuditLogTable({ data, totalCount, limit, setLimit, page, setPage, offset, setOffset }) {
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
    setOffset(limit * newPage);
  };

  const handleChangeRowsPerPage = event => {
    setLimit(+event.target.value);
    setOffset(0);
    setPage(0);
  };

  const useStyles = makeStyles(theme => ({
    root: {
      width: '100%',
      backgroundColor: lighten(theme.palette.background.paper, 0.1),
    },
    container: {
      maxHeight: 440,
    },
  }));
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <TableContainer className={classes.container}>
        <Table stickyHeader aria-label="Scores" size="small">
          <TableHead>
            <TableRow>
              <TableCell>
                <b>#</b>
              </TableCell>
              <TableCell>
                <IntlMessages id={'date'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'username'} />
              </TableCell>
              <TableCell>
                <IntlMessages id={'activity'} />
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.length > 0 &&
              data.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>
                    <b>{index + 1 + offset}</b>
                  </TableCell>
                  <TableCell>{dateFormat(item.actiontime, 'dd/mm/yyyy HH:MM:ss')}</TableCell>
                  <TableCell>{item.pageuser}</TableCell>
                  <TableCell>{item.page}</TableCell>
                </TableRow>
              ))}
          </TableBody>
        </Table>
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[10, 25, 50, 100]}
        component="div"
        count={totalCount}
        backIconButtonProps={page < 1 ? { disabled: true } : { disabled: false }}
        nextIconButtonProps={data.length < limit || data.length === 0 ? { disabled: true } : { disabled: false }}
        rowsPerPage={limit}
        page={page}
        onPageChange={handleChangePage}
        labelRowsPerPage={<IntlMessages id={'rows.per.page'} />}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Paper>
  );
}
