import React, { useEffect, useState, useContext } from 'react';
import { List } from '@material-ui/core';
import NavMenuItem from './NavMenuItem';
import NavSection from './NavSection';
import NavCollapse from './NavCollapse';
import makeStyles from '@material-ui/core/styles/makeStyles';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';
import { CheckboxDatas } from 'routes';

const useStyles = makeStyles(theme => ({
  sideNavMenu: {
    position: 'relative',
  },
}));

const CmtVertical = ({ menuItems }) => {
  const classes = useStyles();

  const [menus, setMenus] = useState([...menuItems]);
  const [allowedUrls, setAllowedUrls] = useState(CheckboxDatas.map(e => e.path));
  useEffect(() => {
    let reportsArr = menuItems[1].children[1].children;
    function checkLS() {
      let storage = localStorage.getItem('favs') || '';
      if (storage !== '') {
        let favorites = storage.split(',');
        let filtered = reportsArr.filter(item => favorites.includes(item.id));
        if (filtered.length > 0) {
          let temp = [...menuItems];
          temp[0].children[2].children = filtered;
          setMenus(temp);
        }
      } else {
        let temp = [...menuItems];
        temp[0].children[2].children = [];
        setMenus(temp);
      }
    }
    checkLS();
    window.addEventListener('storage', checkLS);
    return () => {
      window.removeEventListener('storage', checkLS);
    };
  }, [menuItems]);

  const { rolePages } = useContext(AppContext);
  useEffect(() => {
    let allowedPagesArr = rolePages.split(',');
    let allowedUrlsArr = CheckboxDatas.filter(e => allowedPagesArr.includes(e.id)).map(item => item.path);
    setAllowedUrls(allowedUrlsArr);
  }, [rolePages]);
  return (
    <List component="nav" disablePadding className={classes.sideNavMenu}>
      {menus.map((item, index) => {
        switch (item.type) {
          case 'section':
            return <NavSection {...item} key={index} allowedUrls={allowedUrls} />;
          case 'collapse':
            return <NavCollapse {...item} key={index} />;
          case 'item':
            return <NavMenuItem {...item} key={index} />;
          default:
            return null;
        }
      })}
    </List>
  );
};

export default CmtVertical;
