import React, { useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import { Grid } from '@material-ui/core';
import Filter from './Filter';
import Chart from './Chart';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import FavoriteReports from '@jumbo/utils/FavoriteReports';

const DailyAcc = () => {
  const [response, setResponse] = useState([]);
  const breadcrumbs = [
    { label: <IntlMessages id={'main.reports'} />, link: '/' },
    { label: <IntlMessages id={'dailyacc'} />, isActive: true },
  ];

  return (
    <PageContainer
      heading={
        <>
          <IntlMessages id={'dailyacc'} />
          <FavoriteReports reportId="18" />
        </>
      }
      breadcrumbs={breadcrumbs}>
      <GridContainer>
        <Grid item xs={12} md={12} sm={12} lg={12}>
          <Filter setResponse={setResponse} />
        </Grid>
        <Grid item xs={12} md={12} sm={12} lg={12}>
          <Chart response={response} />
        </Grid>
      </GridContainer>
    </PageContainer>
  );
};

export default DailyAcc;
