import React, { useState } from 'react';
import GridContainer from '@jumbo/components/GridContainer';
import IntlMessages from '@jumbo/utils/IntlMessages';
import { Accordion, AccordionDetails, AccordionSummary, Grid, TextField, Typography } from '@material-ui/core';
import { ExpandMore } from '@material-ui/icons';
import AssignmentIndIcon from '@mui/icons-material/AssignmentInd';
export default function AuditLogFilters({ username, setUsername }) {
  let [filtersOpen, setFiltersOpen] = useState(true);
  return (
    <div style={{ margin: '20px 0' }}>
      <Accordion expanded={filtersOpen}>
        <AccordionSummary expandIcon={<ExpandMore />} onClick={() => setFiltersOpen(!filtersOpen)}>
          <Typography style={{ marginLeft: '10px' }}>
            <IntlMessages id={'filters'} />
          </Typography>
        </AccordionSummary>
        <AccordionDetails style={{ paddingRight: '20px', paddingLeft: '20px' }}>
          <GridContainer>
            <Grid item md={4}>
              <div style={{ color: '#064635' }}>
                <AssignmentIndIcon style={{ verticalAlign: '-5px', marginRight: '.25rem' }} fontSize="medium" />
                <b style={{ fontSize: '18px', fontFamily: 'sans-serif' }}>
                  <IntlMessages id={'username'} />
                </b>
              </div>
              <hr style={{ opacity: '.5', marginTop: '8px' }} />
              <TextField
                fullWidth
                style={{ marginTop: '8px' }}
                variant="outlined"
                id="username"
                type="text"
                name="username"
                value={username}
                label={<IntlMessages id={'username'} />}
                onChange={e => {
                  setUsername(e.target.value);
                }}
              />
            </Grid>
          </GridContainer>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
